<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="proponentes"
      :items-per-page="5"
      class="elevation-1"
      >
      <template v-slot:top>
        <div class="pa-5">
          <v-row justify="space-between" aling="center">
            <h2>Proponentes</h2>
            <v-btn small fab @click="visFomr=!visFomr" color="primary">
              <v-icon dark>add</v-icon>
            </v-btn>
          </v-row>
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            v-if="visFomr"
            >
            <v-row>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="nombre1"
                  :counter="35"
                  :rules="dirNombreRules"
                  label="Nombre 1"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="nombre2"
                  :counter="35"
                  :rules="dirNombreRules"
                  label="Nombre 2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="apellido1"
                  :counter="35"
                  :rules="dirNombreRules"
                  label="Apellido 1"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="apellido2"
                  :counter="35"
                  :rules="dirNombreRules"
                  label="Apellido 2"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="correo"
                  :counter="35"
                  :rules="dirCorreoRules"
                  label="Correo"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row justify="end" no-gutters>
              <v-btn
                color="error"
                class="mr-4"
                @click="reset"
                >Limpiar
              </v-btn>
              <v-btn
                color="accent"
                @click="save"
                >{{btnSave}}
              </v-btn>
            </v-row>
          </v-form>
        </div>
      </template>
      <template v-slot:item.action="{ item }">
        <v-btn icon @click="editProponente(item)" color="primary">
          <v-icon dark>edit
          </v-icon>
        </v-btn>
        <v-btn icon @click="eliminar(item)" color="error">
          <v-icon dark>delete</v-icon>
        </v-btn>
      </template>
    </v-data-table>
    <!-- <pre>{{proponentes}}</pre> -->
  </div>  
</template>
<script>
export default {
  props: ['proponentes'],
  data () {
    return {
      headers: [
        { text: 'Nombre1', value: 'nombre1', align: 'center' },
        { text: 'Nombre2', value: 'nombre2', align: 'center' },
        { text: 'Apellido1', value: 'apellido1', align: 'center' },
        { text: 'Apellido2', value: 'apellido2', align: 'center' },
        { text: 'Email', value: 'correo', align: 'center' },
        { text: 'Opciones', value: 'action', align: 'center' }
      ],
      valid: false,
      nombre1: '',
      nombre2: '',
      apellido1: '',
      apellido2: '',
      correo: '',
      dirNombreRules: [
        v => !!v || 'Nombre es requerido',
        v => (v && v.length <= 35) || 'Nombre debe ser menor de 35 caracteres.',
      ],
      dirApellidoRules: [
        v => !!v || 'Apellido es requerido',
        v => (v && v.length <= 35) || 'Apellido debe ser menor de 35 caracteres.',
      ],
      dirCorreoRules: [
        v => !!v || 'Correo es requerido',
        v => /.+@.+\..+/.test(v) || 'Correo incorrecto',
      ],
      visFomr: false,
      editIndex: '',
    }
  },
  methods: {
    async reset () {
      this.$refs.form.reset()
    },
    async save () {
      const prop = {
        index: this.editIndex,
        nombre1: this.nombre1.trim() ? this.nombre1.toUpperCase() : '',
        nombre2: this.nombre2.trim() ? this.nombre2.toUpperCase() : '',
        apellido1: this.apellido1.trim() ? this.apellido1.toUpperCase() : '',
        apellido2: this.apellido2.trim() ? this.apellido2.toUpperCase() : '',
        correo: this.correo
      }
      if (prop.index || prop.index === 0) this.proponentes.splice(prop.index, 1, prop)
      else this.proponentes.unshift(prop)
      this.editIndex = ''
      this.visFomr = false
    },
    editProponente (item) {
      this.editIndex = this.proponentes.indexOf(item) 
      this.nombre1 = item.nombre1
      this.nombre2 = item.nombre2
      this.apellido1 = item.apellido1
      this.apellido2 = item.apellido2
      this.correo = item.correo
      this.visFomr = true
    },
    async eliminar (item) {
      const index = this.proponentes.indexOf(item)
      this.proponentes.splice(index, 1)
      this.editIndex = ''
      this.visFomr = false
    },
  },
  computed: {
    btnSave () {
      return (this.editIndex || this.editIndex === 0) ? 'Guardar' : 'Agregar'
    }
  }
}
</script>

